import { tpf } from "./data";
import { editDeleteMenu } from "./editDeleteMenu";
import log from "./util.log";
import { mouseDown, mouseUp, mouseMove } from "./mouseEvents";
import { pixelToCoordinate, coordinateToPixel } from "./createTickMarks";

export async function dotsLoad() {
  log('loadDots');

  // Clear existing dots
  const existingDots = document.getElementsByClassName('dot-container');
  while (existingDots.length > 0) {
    existingDots[0].remove();
  }

  try {
    // Get page URL ID from current path
    const urlParts = window.location.pathname.split('/');
    const urlId = urlParts[urlParts.length - 1];

    // Fetch dots from API
    const response = await fetch(`/api/pages/${urlId}/dots`);
    if (!response.ok) throw new Error('Failed to load dots');

    const dots = await response.json();

    // Create dots on the page
    if (dots && dots.length > 0) {
      const xyPlane = document.getElementById("xy-plane");
      if (!xyPlane) return;

      dots.forEach((savedDot: any) => {
        const dot = createDot(savedDot);
        xyPlane.appendChild(dot);
      });
    }
  } catch (error) {
    console.error('Error loading dots:', error);
  }
}

function createDot(savedDot: any): HTMLElement {
  const dot = document.createElement("div");
  dot.classList.add("dot-container");
  dot.style.left = savedDot.x;
  dot.style.top = savedDot.y;

  // Parse existing coordinates if they exist
  let coordinates = savedDot.coordinates;
  if (!coordinates && savedDot.x && savedDot.y) {
    // Extract pixel values from style strings
    const x = parseInt(savedDot.x);
    const y = parseInt(savedDot.y);
    // Convert to graph coordinates
    const graphX = pixelToCoordinate(x);
    const graphY = -pixelToCoordinate(y); // Invert Y coordinate
    coordinates = `(${graphX}, ${graphY})`;
  }

  dot.innerHTML = `
    <div class='dot'></div>
    <div class='coordinate-text'>
      <div class='user-dot-label' style='top:-6px'>${savedDot.label}</div>
      <div class='dot-coordinates'>${coordinates}</div>
    </div>
  `;

  // Add event listeners
  dot.addEventListener("contextmenu", handleContextMenu);
  dot.addEventListener('mousedown', mouseDown);
  dot.addEventListener('mouseup', mouseUp);
  window.addEventListener('mousemove', mouseMove, false);

  return dot;
}

function handleContextMenu(event: Event) {
  log('dot.contextmenu');
  event.preventDefault();

  // Remove any existing menus
  const existingMenus = document.getElementsByClassName('edit-menu');
  while (existingMenus.length > 0) {
    existingMenus[0].remove();
  }

  // Show menu for this dot
  const target = event.target as HTMLElement;
  const dotContainer = findDotContainer(target);
  if (dotContainer) {
    editDeleteMenu(dotContainer);
  }

  tpf.currentDot = null;
}

function findDotContainer(element: HTMLElement): HTMLElement | null {
  if (element.classList.contains('dot-container')) {
    return element;
  }
  if (element.parentElement) {
    return findDotContainer(element.parentElement);
  }
  return null;
}