import log from "./util.log";

export interface Dot {
    x: string;
    y: string;
    coordinates: string;
    label: string;
}

export async function dotsSave(dots: Dot[]) {
    log('saveDots');
    
    try {
        // Get page URL ID from current path
        const urlParts = window.location.pathname.split('/');
        const urlId = urlParts[urlParts.length - 1];
        
        // Save to API
        const response = await fetch(`/api/pages/${urlId}/dots`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                // Add CSRF token if your app uses it
                // 'X-CSRF-Token': getCsrfToken(),
            },
            body: JSON.stringify({ dots }),
        });

        if (!response.ok) {
            throw new Error('Failed to save dots');
        }

        log('Dots saved successfully');
    } catch (error) {
        console.error('Error saving dots:', error);
    }
}
