import { tpf } from "./data";
import { editDeleteMenu } from "./editDeleteMenu";
import { removeAnyExistingElementsWithClassName } from "./removeAnyExistingElementsWithClassName";
import log from "./util.log";
import { mouseDown, mouseUp, mouseMove } from "./mouseEvents";
import { dotsSave } from "./dotsSave";
import { pixelToCoordinate, coordinateToPixel } from "./createTickMarks";

export function dotsCreate() {
    log("createDots");
    const xyPlane = document.getElementById("xy-plane");
    if (!xyPlane) return;
    
    xyPlane.addEventListener('click', xyPlaneClickHandler);
}

function xyPlaneClickHandler(event: MouseEvent) {
    log('xyPlane.click');
    const target = event.target as HTMLElement;
    
    // Don't create dot if clicking on an existing dot
    if (target.classList.contains('dot') || 
        target.classList.contains('dot-container') ||
        target.classList.contains('coordinate-text') ||
        target.classList.contains('user-dot-label') ||
        target.classList.contains('dot-coordinates')) {
        return;
    }

    const currentTarget = event.currentTarget as HTMLElement;
    const rect = currentTarget.getBoundingClientRect();
    const rawX = event.clientX - rect.left;
    const rawY = event.clientY - rect.top;

    const graphX = pixelToCoordinate(rawX);
    const graphY = -pixelToCoordinate(rawY); // Invert Y coordinate since pixels increase downward
    
    // Convert coordinates back to pixels for precise positioning
    const dotX = coordinateToPixel(graphX);
    const dotY = coordinateToPixel(-graphY); // Invert Y coordinate again

    if (isClickInsideGraph({ x: graphX, y: graphY })) {
        log('isClickInsideGraph:branch');
        if (tpf.currentDot === null && !tpf.skipGraphClick) {
            const dot = addDot();
            dot.style.left = `${dotX}px`;
            dot.style.top = `${dotY}px`;
            dot.innerHTML = `
                <div class='dot'></div>
                <div class='coordinate-text'>
                    <div class='user-dot-label' style='top:-6px'>null</div>
                    <div class='dot-coordinates'>(${graphX}, ${graphY})</div>
                </div>
            `;
            
            dot.addEventListener("contextmenu", mouseContextmenu);
            dot.addEventListener('mousedown', mouseDown);
            dot.addEventListener('mouseup', mouseUp);
            window.addEventListener('mousemove', mouseMove, false);
            
            currentTarget.appendChild(dot);
            
            // Save the new dot
            const dots = Array.from(document.getElementsByClassName('dot-container')).map(dotEl => {
                const labelElement = dotEl.querySelector('.user-dot-label');
                const coordsElement = dotEl.querySelector('.dot-coordinates');
                return {
                    x: (dotEl as HTMLElement).style.left,
                    y: (dotEl as HTMLElement).style.top,
                    coordinates: coordsElement?.textContent || '',
                    label: labelElement?.textContent || 'null'
                };
            });
            dotsSave(dots);
        }
        tpf.skipGraphClick = false;
    }
}

function mouseContextmenu(event: MouseEvent) {
    log('dot.contextmenu');
    event.preventDefault();
    removeAnyExistingElementsWithClassName('edit-menu');
    editDeleteMenu(event.target);
    tpf.currentDot = null;
}

function isClickInsideGraph(coords: { x: number; y: number; }) {
    return coords.x >= -5 && 
           coords.x <= 5 && 
           coords.y >= -5 && 
           coords.y <= 5;
}

function addDot() {
    const dot = document.createElement("div");
    dot.classList.add("dot-container");
    return dot;
}