import { tpf } from "./data";
import log from "./util.log";
import { dotsSave } from "./dotsSave";

export function editDeleteMenu(target: any) {
    log('editDeleteMenu');
    
    // Create menu container
    const menu = document.createElement('div');
    menu.className = 'edit-menu';
    menu.style.position = 'absolute';
    menu.style.zIndex = '1000';
    menu.style.backgroundColor = 'white';
    menu.style.border = '1px solid black';
    menu.style.padding = '5px';
    menu.style.borderRadius = '3px';
    menu.style.cursor = 'pointer';

    // Create edit button
    const editButton = document.createElement('div');
    editButton.innerText = 'Edit Label';
    editButton.addEventListener('click', () => handleEdit(target));
    menu.appendChild(editButton);

    // Create delete button
    const deleteButton = document.createElement('div');
    deleteButton.innerText = 'Delete';
    deleteButton.addEventListener('click', () => handleDelete(target));
    menu.appendChild(deleteButton);

    // Position menu near target
    const rect = target.getBoundingClientRect();
    menu.style.left = rect.left + window.scrollX + 'px';
    menu.style.top = rect.bottom + window.scrollY + 'px';

    // Add menu to document
    document.body.appendChild(menu);

    // Close menu when clicking outside
    document.addEventListener('click', function closeMenu(e: MouseEvent) {
        if (!menu.contains(e.target as Node)) {
            menu.remove();
            document.removeEventListener('click', closeMenu);
        }
    });
}

function handleEdit(target: HTMLElement) {
    log('handleEdit');
    
    // Get current label
    const labelElement = target.querySelector('.user-dot-label') as HTMLElement;
    if (!labelElement) return;
    
    // Create input field
    const input = document.createElement('input');
    input.type = 'text';
    input.value = labelElement.innerText === 'null' ? '' : labelElement.innerText;
    input.style.position = 'absolute';
    input.style.zIndex = '1000';
    
    // Position input
    const rect = labelElement.getBoundingClientRect();
    input.style.left = rect.left + window.scrollX + 'px';
    input.style.top = rect.top + window.scrollY + 'px';
    
    // Handle input events
    input.addEventListener('blur', finishEdit);
    input.addEventListener('keypress', (e) => {
        if (e.key === 'Enter') {
            finishEdit();
        }
    });

    function finishEdit() {
        labelElement.innerText = input.value || 'null';
        input.remove();
        saveDots();
    }

    // Add input to document
    document.body.appendChild(input);
    input.focus();
}

function handleDelete(target: HTMLElement) {
    log('handleDelete');
    target.remove();
    saveDots();
}

function saveDots() {
    log('saveDots');
    const dots = Array.from(document.getElementsByClassName('dot-container')).map(dot => {
        const labelElement = dot.querySelector('.user-dot-label');
        const coordsElement = dot.querySelector('.dot-coordinates');
        return {
            x: (dot as HTMLElement).style.left,
            y: (dot as HTMLElement).style.top,
            coordinates: coordsElement?.textContent || '',
            label: labelElement?.textContent || 'null'
        };
    });
    dotsSave(dots);
}
