import { createTickMarks } from "./createTickMarks";
import { dotsCreate } from "./dotsCreate";
import { dotsLoad } from "./dotsLoad";
import log from "./util.log";

// Initialize when DOM is loaded
document.addEventListener('DOMContentLoaded', () => {
  log('Initializing application');
  init();
});

function init() {
  try {
    // Only initialize if we're on a page with the xy-plane
    const xyPlane = document.getElementById('xy-plane');
    if (xyPlane) {
      dotsLoad();
      createTickMarksWithErrorHandling(xyPlane);
      dotsCreate();
      log('Application initialized successfully');
    } else {
      console.error('xyPlane is null, unable to initialize application');
    }
  } catch (error) {
    console.error('Error initializing application:', error);
  }
}

function createTickMarksWithErrorHandling(xyPlane: HTMLElement) {
  try {
    createTickMarks(xyPlane);
  } catch (error) {
    console.error('Error creating tick marks:', error);
  }
}