import log from './util.log';

export function createTickMarks(xyPlane: HTMLElement) {
  log('createTickMarks');

  const PLANE_SIZE = 400;
  const TICK_COUNT = 11;
  const SEGMENT_SIZE = PLANE_SIZE / 10; // 40 pixels per unit

  function createTicks(isXAxis: boolean) {
    const positions = Array.from({ length: TICK_COUNT }, (_, i) => i * (PLANE_SIZE / (TICK_COUNT - 1)));

    const existingTicks = Array.from(xyPlane.getElementsByClassName(isXAxis ? 'x-tick' : 'y-tick'));
    const existingLabels = Array.from(xyPlane.getElementsByClassName(isXAxis ? 'x-label' : 'y-label'));
    existingTicks.forEach(tick => tick.remove());
    existingLabels.forEach(label => label.remove());

    positions.forEach((pos, index) => {
      const tick = document.createElement('div');
      tick.className = isXAxis ? 'x-tick' : 'y-tick';
      tick.style.backgroundColor = (index === Math.floor(TICK_COUNT / 2)) ? 'lightgray' : 'black';
      tick.style.width = isXAxis ? '1px' : (index === Math.floor(TICK_COUNT / 2) ? '2px' : '10px');
      tick.style.height = isXAxis ? (index === Math.floor(TICK_COUNT / 2) ? '2px' : '10px') : '1px';

      if (isXAxis) {
        tick.style.left = `${pos}px`;
        tick.style.top = '50%';
        tick.style.transform = 'translateY(-50%)';
      } else {
        tick.style.top = `${pos}px`;
        tick.style.left = '50%';
        tick.style.transform = 'translateX(-50%)';
      }

      if (index !== Math.floor(TICK_COUNT / 2)) {
        const label = document.createElement('div');
        label.className = isXAxis ? 'x-label' : 'y-label';
        label.style.color = 'black';
        label.style.fontSize = '14px';
        label.style.position = 'absolute';
        label.style.whiteSpace = 'nowrap';

        if (isXAxis) {
          if (index === 0) {
            label.textContent = 'Never';
            label.style.left = 'calc(-10%)';
            label.style.top = 'calc(50% - 25px)';
            label.style.transform = 'translateX(0)';
            xyPlane.appendChild(label);

            const doLabel = document.createElement('div');
            doLabel.textContent = 'Do';
            doLabel.className = 'x-label';
            doLabel.style.left = 'calc(-10%)';
            doLabel.style.top = 'calc(50% - 5px)';
            doLabel.style.transform = 'translateX(0)';
            xyPlane.appendChild(doLabel);
          } else if (index === TICK_COUNT - 1) {
            label.textContent = 'Always';
            label.style.left = 'calc(110%)';
            label.style.top = 'calc(50% - 25px)';
            label.style.transform = 'translateX(-100%)';
            xyPlane.appendChild(label);

            const doLabel = document.createElement('div');
            doLabel.textContent = 'Do';
            doLabel.className = 'x-label';
            doLabel.style.left = 'calc(110%)';
            doLabel.style.top = 'calc(50% - 5px)';
            doLabel.style.transform = 'translateX(-100%)';
            xyPlane.appendChild(doLabel);
          }
        } else {
          if (index === 0) {
            label.textContent = 'Helpful';
            label.style.top = 'calc(-1%)';
            label.style.left = '50%';
            label.style.transform = 'translateX(-50%) translateY(-100%)';
            xyPlane.appendChild(label);
            setTimeout(() => {
              const labelWidth = label.offsetWidth;
              label.style.left = `calc(54% - ${labelWidth / 2}px)`;
            }, 0);
          } else if (index === TICK_COUNT - 1) {
            label.textContent = 'Unhelpful';
            label.style.top = 'calc(100.5%)';
            label.style.left = '50%';
            label.style.transform = 'translateX(-50%) translateY(0%)';
            xyPlane.appendChild(label);
            setTimeout(() => {
              const labelWidth = label.offsetWidth;
              label.style.left = `calc(53% - ${labelWidth / 2}px - 4px)`;
            }, 0);
          }
        }
      }

      xyPlane.appendChild(tick);
    });
  }

  createTicks(true);
  createTicks(false);
}

export function pixelToCoordinate(pixel: number): number {
  const PLANE_SIZE = 400;
  const SEGMENT_SIZE = PLANE_SIZE / 10; // 40 pixels per unit
  return Math.round(((pixel - PLANE_SIZE / 2) / SEGMENT_SIZE) * 10) / 10;
}

export function coordinateToPixel(coordinate: number): number {
  const PLANE_SIZE = 400;
  const SEGMENT_SIZE = PLANE_SIZE / 10; // 40 pixels per unit
  const pixels = (coordinate * SEGMENT_SIZE) + (PLANE_SIZE / 2);
  return Math.max(0, Math.min(PLANE_SIZE, pixels));
}