import { tpf } from "./data";
import log from "./util.log";
import { dotsSave } from "./dotsSave";

export function mouseDown(event: MouseEvent) {
    log('mouseDown');
    const target = event.target as HTMLElement;
    const dotContainer = findDotContainer(target);
    
    if (dotContainer) {
        event.preventDefault();
        tpf.isDragging = true;
        tpf.currentDot = dotContainer as HTMLDivElement;
        
        const rect = dotContainer.getBoundingClientRect();
        tpf.offsetX = event.clientX - rect.left;
        tpf.offsetY = event.clientY - rect.top;
    }
}

export function mouseUp(event: MouseEvent) {
    log('mouseUp');
    if (tpf.isDragging && tpf.currentDot) {
        tpf.isDragging = false;
        updateDotCoordinates(tpf.currentDot);
        saveDots();
    }
}

export function mouseMove(event: MouseEvent) {
    if (!tpf.isDragging || !tpf.currentDot) return;
    
    log('mouseMove');
    event.preventDefault();
    
    const xyPlane = document.getElementById('xy-plane');
    if (!xyPlane) return;
    
    const rect = xyPlane.getBoundingClientRect();
    const x = event.clientX - rect.left - tpf.offsetX;
    const y = event.clientY - rect.top - tpf.offsetY;
    
    if (isWithinBounds(x, y, rect)) {
        tpf.currentDot.style.left = `${x}px`;
        tpf.currentDot.style.top = `${y}px`;
        updateDotCoordinates(tpf.currentDot);
    }
}

function findDotContainer(element: HTMLElement): HTMLDivElement | null {
    if (element.classList.contains('dot-container')) {
        return element as HTMLDivElement;
    }
    if (element.parentElement) {
        return findDotContainer(element.parentElement);
    }
    return null;
}

function isWithinBounds(x: number, y: number, rect: DOMRect): boolean {
    return x >= 0 && x <= rect.width && y >= 0 && y <= rect.height;
}

function updateDotCoordinates(dot: HTMLElement) {
    const xyPlane = document.getElementById('xy-plane');
    if (!xyPlane) return;
    
    const rect = xyPlane.getBoundingClientRect();
    const x = parseFloat(dot.style.left);
    const y = parseFloat(dot.style.top);
    
    const graphX = Math.round((x - rect.width / 2) / (rect.width / 10) * 10) / 10;
    const graphY = Math.round((rect.height / 2 - y) / (rect.height / 10) * 10) / 10;
    
    const coordsElement = dot.querySelector('.dot-coordinates');
    if (coordsElement) {
        coordsElement.textContent = `(${graphX.toFixed(1)}, ${graphY.toFixed(1)})`;
    }
}

function saveDots() {
    const dots = Array.from(document.getElementsByClassName('dot-container')).map(dot => {
        const labelElement = dot.querySelector('.user-dot-label');
        const coordsElement = dot.querySelector('.dot-coordinates');
        return {
            x: (dot as HTMLElement).style.left,
            y: (dot as HTMLElement).style.top,
            coordinates: coordsElement?.textContent || '',
            label: labelElement?.textContent || 'null'
        };
    });
    dotsSave(dots);
}