interface TPF {
    skipGraphClick: boolean;
    isDragging: boolean;
    debug: boolean;
    offsetX: number;
    offsetY: number;
    currentDot: HTMLDivElement | null | undefined;
    xAxisWidth: number;
    yAxisHeight: number;
}

// Initialize with default values, we'll update these after DOM load
let tpf: TPF = {
    skipGraphClick: false,
    isDragging: false,
    debug: true,
    offsetX: 0,
    offsetY: 0,
    currentDot: null,
    xAxisWidth: 0,
    yAxisHeight: 0
};

// Update dimensions after DOM load
document.addEventListener('DOMContentLoaded', () => {
    const xAxis = document.getElementsByClassName("x-axis")[0] as HTMLElement;
    const yAxis = document.getElementsByClassName("y-axis")[0] as HTMLElement;
    if (xAxis && yAxis) {
        tpf.xAxisWidth = xAxis.clientWidth;
        tpf.yAxisHeight = yAxis.clientHeight;
    }
});

export { tpf };